// GENERAL -- Every boring rule that applies to the whole page or small elements used repeatedly goes here

@font-face {
	font-family: "Tsukimi Rounded Light";
	src: url('./fonts/TsukimiRounded-Light.ttf');
}

@font-face {
	font-family: "Tsukimi Rounded Regular";
	src: url('./fonts/TsukimiRounded-Regular.ttf');
}

@font-face {
	font-family: "Tsukimi Rounded Medium";
	src: url('./fonts/TsukimiRounded-Medium.ttf');
}

@font-face {
	font-family: "Tsukimi Rounded SemiBold";
	src: url('./fonts/TsukimiRounded-SemiBold.ttf');
}

* {
	box-sizing: border-box;
}

html {
	font-family: "Tsukimi Rounded SemiBold", sans-serif;
	color: rgba(0, 0, 0, 0.87);
	margin: 0;
	overflow-x: hidden;
	max-width: 100%;
}

body {
	cursor: url('cursor.png'), auto;
}

a {
	color: #ffabab;
}

.title-logo {
	max-width: 100%;
	max-height: 100vh;
	margin: auto;
	filter: contrast(105%);
	animation: floatUpDown 3s ease-in-out infinite alternate; /* Adjust timing and animation as needed */
}

.button {
	box-shadow: 0 3px 6px rgba(0,0,0,0.25), inset 0 -4px 0 rgba(0,0,0,0.2);
	min-width: 150px;
	display: inline-flex !important;
	background-color: #ffabab;
	padding: 15px 20px !important;
	text-align: center !important;
	border-radius: 15px;
	color: #fff;
	cursor: pointer;
	overflow-wrap: break-word;
	vertical-align: middle;
	border: 0 none #fff !important;
	text-decoration: none;
	text-shadow: none;
	font-weight: 600 !important;
	font-size: 20px !important;
	margin: 10px;
	transition: all linear 0.1s;
	
	.fa-brands, .fa-solid {
		margin: 0px 5px !important;
		font-size: 22px;
		vertical-align: middle;
	}

	&.kofi {
		color: #fff;
		background-color: #29abe0;
	}

	&.reddit {
		color: #ff4500;
		background-color: #fff;
	}

	&:hover {
		border-radius: 10px;
		filter: brightness(1.15); // Too lazy to configure the colors manually
	}

	&:active {
		box-shadow: none;
	}
}

.select-wrapper {
	font-size: 20px;

	.select-button {
		background: #eee;
		padding: 0 1em;
		padding-right: 3em;
		box-shadow: 0 3px 6px rgba(0,0,0,0.25);
		height: 2em;
		display: flex;
		align-items: center;


		.fa-solid {
			position: absolute;
			top: 0;
			right: 0;
			width: 2em;
			height: 2em;
			display: flex;
			justify-content: center;
			align-items: center;
			border-left: 1px solid #ccc;
		}
	}

	select {
		position: absolute;
		top:0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		cursor: pointer;
		font-size: 16px;
	}

	position: relative;
	min-width: 125px;
}

::selection {
	background: #ff6684;
	color: #ffced8;
}
