@keyframes floatUpDown {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-30px); /* Adjust the distance you want it to move */
  }
}
@font-face {
  font-family: "Tsukimi Rounded Light";
  src: url("./fonts/TsukimiRounded-Light.ttf");
}
@font-face {
  font-family: "Tsukimi Rounded Regular";
  src: url("./fonts/TsukimiRounded-Regular.ttf");
}
@font-face {
  font-family: "Tsukimi Rounded Medium";
  src: url("./fonts/TsukimiRounded-Medium.ttf");
}
@font-face {
  font-family: "Tsukimi Rounded SemiBold";
  src: url("./fonts/TsukimiRounded-SemiBold.ttf");
}
* {
  box-sizing: border-box;
}

html {
  font-family: "Tsukimi Rounded SemiBold", sans-serif;
  color: rgba(0, 0, 0, 0.87);
  margin: 0;
  overflow-x: hidden;
  max-width: 100%;
}

body {
  cursor: url("cursor.png"), auto;
}

a {
  color: #ffabab;
}

.title-logo {
  max-width: 100%;
  max-height: 100vh;
  margin: auto;
  filter: contrast(105%);
  animation: floatUpDown 3s ease-in-out infinite alternate; /* Adjust timing and animation as needed */
}

.button {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.25), inset 0 -4px 0 rgba(0, 0, 0, 0.2);
  min-width: 150px;
  display: inline-flex !important;
  background-color: #ffabab;
  padding: 15px 20px !important;
  text-align: center !important;
  border-radius: 15px;
  color: #fff;
  cursor: pointer;
  overflow-wrap: break-word;
  vertical-align: middle;
  border: 0 none #fff !important;
  text-decoration: none;
  text-shadow: none;
  font-weight: 600 !important;
  font-size: 20px !important;
  margin: 10px;
  transition: all linear 0.1s;
}
.button .fa-brands, .button .fa-solid {
  margin: 0px 5px !important;
  font-size: 22px;
  vertical-align: middle;
}
.button.kofi {
  color: #fff;
  background-color: #29abe0;
}
.button.reddit {
  color: #ff4500;
  background-color: #fff;
}
.button:hover {
  border-radius: 10px;
  filter: brightness(1.15);
}
.button:active {
  box-shadow: none;
}

.select-wrapper {
  font-size: 20px;
  position: relative;
  min-width: 125px;
}
.select-wrapper .select-button {
  background: #eee;
  padding: 0 1em;
  padding-right: 3em;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.25);
  height: 2em;
  display: flex;
  align-items: center;
}
.select-wrapper .select-button .fa-solid {
  position: absolute;
  top: 0;
  right: 0;
  width: 2em;
  height: 2em;
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: 1px solid #ccc;
}
.select-wrapper select {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  font-size: 16px;
}

::selection {
  background: #ff6684;
  color: #ffced8;
}
.main {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  background-size: cover;
  background-position: top right;
  background-attachment: fixed;
}
.main .button-container {
  position: absolute;
  top: 80%;
  display: inline-flex;
}
.main .scroll-notice {
  width: 100%;
  position: absolute;
  left: 0;
  top: 95%;
  font-size: 18pt;
}
.main .scroll-notice .fa-solid {
  font-size: 20pt;
  vertical-align: middle;
  margin: 0px 10px !important;
}

.main .title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #fff;
  text-align: center;
  text-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);
}

.page {
  background-image: url("./img/HellBG.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: auto 100%;
  background-position: center;
}
.has-webp .page {
  background-image: url("./img/webp/HellBG.webp");
}

.info-stuff {
  padding-top: 50px;
  background-position: relative;
  background-image: url("./hellish-pattern.png");
  background-repeat: repeat;
  -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgb(14, 4, 4) 10%);
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgb(14, 4, 4) 10%);
}

.back-overlay {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.75) 100%);
  backdrop-filter: contrast(1.25);
}

.section {
  width: 100%;
  overflow: hidden;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 30px;
  color: #fff;
  min-height: 500px;
}
.section .center,
.section .left,
.section .right {
  display: inline-block;
  margin: 0 30px;
}
.section .image {
  flex: 1;
  position: relative;
  height: 100%;
  display: flex;
}
.section .left {
  text-align: right;
}
.section .image.left {
  justify-content: flex-end;
}
.section .image.right {
  justify-content: flex-start;
}
.section .center {
  width: 100%;
  max-width: 1024px;
  text-align: justify;
  justify-content: space-evenly;
}
.section h2 {
  z-index: 10;
  font-size: 42px;
  font-weight: 500;
  margin: 5px 0;
}
.section p {
  font-family: "Tsukimi Rounded Regular", sans-serif;
  z-index: 10;
  font-size: 24px;
  font-weight: 300;
  line-height: 1.4;
  margin: 24px 0 12px;
}
.section p b {
  font-family: "Tsukimi Rounded Medium", sans-serif;
}
.section .small {
  font-family: "Tsukimi Rounded Light", sans-serif;
  font-size: 14px;
  display: block;
  opacity: 0.65;
}
.section .small b {
  font-family: "Tsukimi Rounded Regular", sans-serif;
}
@media only screen and (max-width: 480px), only screen and (max-device-width: 480px) {
  @keyframes floatUpDown {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(-10px); /* Adjust the distance you want it to move */
    }
  }
  .main .button-container {
    position: absolute;
    top: 70%;
    display: inline;
  }
  .main .scroll-notice {
    width: 100%;
    position: absolute;
    left: 0;
    top: 96%;
    font-size: 15pt;
  }
  .main .scroll-notice .fa-solid {
    font-size: 15pt;
    vertical-align: middle;
    margin-right: 7px;
  }
  .button {
    min-width: 75px;
    padding: 12px 12px !important;
    font-size: 15px !important;
    margin: 5px;
  }
  .button .fa-brands, .button .fa-solid {
    margin-right: 5px !important;
    font-size: 17px;
  }
  .section {
    flex-flow: column;
    display: flex;
    padding: 10px;
  }
  .section .center,
  .section .left,
  .section .right {
    text-align: center;
    margin: 0 10px;
  }
  .section .center {
    max-width: 85%;
  }
  .section img {
    padding: 5px;
    height: auto;
    width: 100%;
  }
  .section h2 {
    font-size: 32px;
  }
  .section p {
    font-size: 14px;
  }
  .section .small {
    font-size: 10px;
  }
}