// LAYOUT -- Every rules that relates to the general layout of the website goes here

.main {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100vh;
	position: relative;
	overflow: hidden;
	background-size: cover;
	background-position: top right;
	background-attachment: fixed;

	.button-container {
		position: absolute;
		top: 80%;
		display: inline-flex;
	}

	.scroll-notice {
		width: 100%;
		position: absolute;
		left: 0;
		top: 95%;
		font-size: 18pt;

		.fa-solid {
			font-size: 20pt;
			vertical-align: middle;
			margin: 0px 10px !important;
		}
	}
}

.main .title {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	color: #fff;
	text-align: center;
	text-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);
}

.page {
	background-image: url("./img/HellBG.jpg");
	background-repeat: no-repeat;
	background-attachment: fixed;
    background-size: auto 100%;
    background-position: center;

	.has-webp & {
		background-image: url("./img/webp/HellBG.webp");
	}
}

.info-stuff {
	padding-top: 50px;
	background-position: relative;
	background-image: url("./hellish-pattern.png");
	background-repeat: repeat;
	
	-webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgb(14, 4, 4, 1) 10%);
	mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgb(14, 4, 4, 1) 10%);
}

.back-overlay {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.75) 100%);
	backdrop-filter: contrast(1.25);
}

.section {
	width: 100%;
	overflow: hidden;
	margin: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	padding: 30px;
	color: #fff;
	min-height: 500px;

	.center,
	.left,
	.right {
		display: inline-block;
		margin: 0 30px;
	}

	.image {
		flex: 1;
		position: relative;
		height: 100%;
		display: flex;
	}

	.left {
		text-align: right;
	}

	.image.left {
		justify-content: flex-end;
	}

	.image.right {
		justify-content: flex-start;
	}

	.center {
		width: 100%;
		max-width: 1024px;
		text-align: justify;
		justify-content: space-evenly;
	}

	h2 {
		z-index: 10;
		font-size: 42px;
		font-weight: 500;
		margin: 5px 0;
	}

	p {
		font-family: "Tsukimi Rounded Regular", sans-serif;
		z-index: 10;
		font-size: 24px;
		font-weight: 300;
		line-height: 1.4;
		margin: 24px 0 12px;

		b {
			font-family: "Tsukimi Rounded Medium", sans-serif;
		}
	}

	.small {
		font-family: "Tsukimi Rounded Light", sans-serif;
		font-size: 14px;
		display: block;
		opacity: 0.65;

		b {
			font-family: "Tsukimi Rounded Regular", sans-serif;
		}
	}
}