// MOBILE
@media only screen and (max-width: 480px), only screen and (max-device-width: 480px) {
	@keyframes floatUpDown {
		0% {
			transform: translateY(0);
		}
		100% {
			transform: translateY(-10px); /* Adjust the distance you want it to move */
		}
	}
	
	.main {
		.button-container {
			position: absolute;
			top: 70%;
			display: inline;
		}

		.scroll-notice {
			width: 100%;
			position: absolute;
			left: 0;
			top: 96%;
			font-size: 15pt;
	
			.fa-solid {
				font-size: 15pt;
				vertical-align: middle;
				margin-right: 7px;
			}
		}
	}

	.button {
		min-width: 75px;
		padding: 12px 12px !important;
		font-size: 15px !important;
		margin: 5px;
		
		.fa-brands, .fa-solid {
			margin-right: 5px !important;
			font-size: 17px;
		}
	}

	.section {
		flex-flow: column;
		display: flex;
		padding: 10px;
	
		.center,
		.left,
		.right {
			text-align: center;
			margin: 0 10px;
		}

		.center {
			max-width: 85%;
		}
	
		img {
			padding: 5px;
			height: auto;
			width: 100%;
		}
	
		h2 {
			font-size: 32px;
		}
	
		p {
			font-size: 14px;
		}
	
		.small {
			font-size: 10px;
		}
	}
}